export default {
  name: 'DataForm',
  data () {
    return {
      name: null,
      phone: null,
      persons: [],
      accept: false,
      expanded: true,
      message: '',
      csvContent: '',
      alert: false,
      confirm: false,
      prompt: false,
      address: ''
    }
  },

  methods: {
    onClick (url) {
      window.open(url)
    },
    sendForall () {
      let message = this.message
      this.persons.forEach((element, index, array) => {
        let messageTransform = message.replace('[nome]', element.name)
        let messageEmcoded = encodeURI(messageTransform)
        window.open(element.url + '&text=' + messageEmcoded)
      }, message)
    },
    importCsv () {
      const allText = this.csvContent
      const allTextLines = allText.split(/\r\n|\n/)

      allTextLines.forEach((element, index, array) => {
        if (element !== 'nome,telefone') {
          const data = element.split(',')
          this.persons.push({
            name: data[0],
            phone: data[1],
            url: 'http://api.whatsapp.com/send?phone=55' + data[1]
          })
        }
      })

      this.prompt = !this.prompt

      this.$q.notify({
        message: 'Conteúdo importado',
        color: 'green'
      })
    },
    onSubmit () {
      this.persons.push({
        name: this.name,
        phone: this.phone,
        url: 'http://api.whatsapp.com/send?phone=55' + this.phone
      })
      this.$q.notify({
        message: 'Número adicionado a lista',
        color: 'green'
      })
    }
  }
}
